<template>
    <div class="public_content">
        <div class="public_title">
          <span>二手房录入房源</span>
          <el-button class="fr" icon="el-icon-back" size="small" @click='$router.push({name: "secondhouselists"})'>房源库</el-button>
        </div> 
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">



            <el-form-item label="小区名称" prop="community_id">
              <el-select
                @blur='blurMethod'
                style="width: 250px;margin-right:20px;"
                v-model="ruleForm.community_id"
                filterable
                remote
                reserve-keyword
                placeholder="请输入关键词"
                :remote-method="remoteMethod"
                :loading="loading"> 
                <el-option
                  v-for="item in community_id"
                  :key="item.id"
                  :label="item.communityname"
                  :value="item.id">
                </el-option>
              </el-select>  
                <el-button type="primary" round v-show="community_id.length == 0 && temp" @click="addCommunity">录入小区</el-button>
            </el-form-item>
 


            <el-form-item label="房屋情况" prop="housetype" class="inlineBlock">
                <el-select v-model="ruleForm.housetype" placeholder="房屋类型">
                    <el-option
                      v-for="item in housetype"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select> 
            </el-form-item>

            <el-form-item prop="fixture" label-width="0" class="inlineBlock">
                <el-select v-model="ruleForm.fixture" placeholder="装修程度">
                    <el-option
                      v-for="item in decoration"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select> 
            </el-form-item>
            <el-form-item prop="towards" label-width="0" class="inlineBlock">
                <el-select v-model="ruleForm.towards" placeholder="房屋朝向">
                    <el-option
                      v-for="item in aspect"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>

          <div>
            <el-form-item label="户型面积" prop="bedroom" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.bedroom"><span slot="suffix">室</span></el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="livingroon" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.livingroon"><span slot="suffix">厅</span></el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="bathroom" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.bathroom"><span slot="suffix">卫</span></el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="area" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.area"><span slot="suffix">㎡</span></el-input>
            </el-form-item>
          </div>

          <el-form-item label="房屋售价" prop="price">
            <el-input style="width:150px;" v-model="ruleForm.price" placeholder="0元表示面议"><span slot="suffix">万元</span></el-input>
          </el-form-item>
          
            <el-form-item label="房屋标题" prop="title">
                <el-input style="width:300px;" v-model="houseTitle"></el-input>
            </el-form-item>

           

            <div>

            
             <el-form-item label="房屋楼层" prop="floor" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.floor">
                  <span slot="prefix">第</span>
                  <span slot="suffix">层</span>
                </el-input>
             </el-form-item>
             <el-form-item label-width="0" prop="totalfloor" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.totalfloor">
                  <span slot="prefix">共</span>
                  <span slot="suffix">层</span>
                </el-input>
            </el-form-item>
            </div>
            <div>
             <el-form-item label="房本年限" prop="certificate" class="inlineBlock">
                <el-select v-model="ruleForm.certificate" placeholder="年限" style="width: 120px;">
                    <el-option
                      v-for="item in certificate"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select> 
             </el-form-item>
             <el-form-item label-width="0" prop="only" class="inlineBlock">
                <el-select v-model="ruleForm.only" placeholder="是否唯一" style="width: 120px;">
                    <el-option
                      v-for="item in only"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select>  
            </el-form-item>
            </div>


            <el-form-item label="封面图片" prop="thumb"> 
               
              <upload-img  v-if="dataSuccess" :data='ruleForm.thumb' @onSuccess="changeLogo($event,'thumb')" style="display: inline-block"></upload-img>

              <span class="public_littleTip"><i class="iconfont">&#xe670;</i>单图，供列表页使用</span>

               
            </el-form-item>

            <el-form-item label="房源图片" prop="images">
               <upload-imgs  v-if="dataSuccess" :data='ruleForm.images' @onSuccess="changeImages"></upload-imgs>
                
            </el-form-item>  


            <!-- 上传视频 -->
            <el-form-item label="上传视频" prop="video">
              <upload-video  v-if="dataSuccess" :data='ruleForm.video'  @onSuccess='changeVideo'></upload-video>

            </el-form-item> 


            <el-form-item label="VR看房" prop="panorama">
              <el-input v-model="ruleForm.panorama" style="width: 300px">
                    <template slot="prepend">Http://</template>
              </el-input>
              <span class="public_littleTip"><i class="iconfont">&#xe670;</i>请输入VR文件的URL</span>

            </el-form-item>

            <el-form-item label="特色标签" prop="tag">
                <el-checkbox-group v-model="ruleForm.tag">
                    <el-checkbox v-for="item in tag" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox>
                </el-checkbox-group>
            </el-form-item> 

            <el-form-item label="房屋描述" prop="description">

              <editor :value="ruleForm.description" 
                        @show="editors">
              </editor> 
              <!-- <el-input style="width: 700px" type="textarea" :rows="6"  v-model="ruleForm.description">
              </el-input> -->
            </el-form-item>
             <el-form-item label="业主心态" prop="mentality" style="margin-bottom: 120px;">
              <el-input style="width: 700px" type="textarea" :rows="6"  v-model="ruleForm.mentality">
              </el-input>
            </el-form-item>

            <el-button type="primary" round @click="submitForm('ruleForm')" class="public_submit">确认提交</el-button>
         </el-form>
    </div>
</template>

<script>
import { salePublish, getCommunity } from "@/api/house";
import uploadImg from "@/components/upload/uploadimg";
import uploadImgs from "@/components/upload/uploadimgs";
import uploadVideo from "@/components/upload/uploadVideo";
import editor from "@/components/tinymce/editor";

export default {
  components: {
    uploadImg,
    uploadImgs,
    uploadVideo,
    editor
  },
  data() {
    return {
      dataSuccess: false,
      showadd: false,
      temp: "", // 临时存储
      community_id: [], // 小区列表
      loading: false,
      housetype: [],
      decoration: [],
      aspect: [],
      certificate: [
        {
          id: 0,
          paramname: "不满两年"
        },
        {
          id: 1,
          paramname: "满两年"
        },
        {
          id: 2,
          paramname: "满五年"
        }
      ],
      only: [
        {
          id: 0,
          paramname: "否"
        },
        {
          id: 1,
          paramname: "是"
        }
      ],
      tag: [],
      ruleForm: {
        community_id: "",
        housetype: "",
        fixture: "",
        towards: "",
        bedroom: "",
        livingroon: "",
        bathroom: "",
        area: "",
        floor: "",
        totalfloor: "",
        certificate: "",
        only: "",
        thumb: "",
        images: "", // 房源图片
        video: "",
        panorama: "",
        description: "",
        mentality: "",
        title: "",
        price: "",
        tag: [] // checbox 必须是数组
      },
      rules: {
        community_id: [
          { required: true, message: "请输入小区名称", trigger: "change" }
        ],
        housetype: [{ required: true, message: "请选择房屋情况" }],
        fixture: [{ required: true, message: "请选择装修程度" }],
        towards: [{ required: true, message: "请选择房屋朝向" }],
        bedroom: [{ required: true, message: "请输入室" }],
        livingroon: [{ required: true, message: "请输入厅" }],
        bathroom: [{ required: true, message: "请输入卫" }],
        area: [{ required: true, message: "请输入面积" }],
        title: [{ required: true, message: "请输入房屋标题" }],
        price: [{ required: true, message: "请输入房屋售价" }],
        floor: [{ required: true, message: "请输入所在楼层" }],
        totalfloor: [{ required: true, message: "请输入总楼层" }],
        certificate: [{ required: true, message: "请选择房屋年限" }],
        only: [{ required: true, message: "请选择是否唯一" }]
      }
    };
  },

  created() {
    if (this.$route.params.search) {
      this.remoteMethod(this.$route.params.search);
    }

    if (this.$route.query.id) {
      salePublish("get", this.$route.query.id).then(res => {
        if (res.data.code == 1) {
          let data = res.data.data.agent;
          this.remoteMethod(data.communityname).then(res => {
            this.ruleForm = data;
            this.ruleForm.tag = data.tag.map(item => {
              return Number(item);
            });
            this.dataSuccess = true;
          });
        }
      });
    } else {
      this.dataSuccess = true;
    }

    salePublish("get").then(res => {
      this.tag = res.data.data.tag;
      this.decoration = res.data.data.decoration;
      this.aspect = res.data.data.aspect;
      this.housetype = res.data.data.housetype;
    });
  },

  computed: {
    fixture() {
      let r = "";
      if (this.ruleForm.fixture) {
        this.decoration.forEach(element => {
          if (element.id == this.ruleForm.fixture) {
            r = element.paramname;
          }
        });
        return r;
      } else {
        return "";
      }
    },
    houseName() {
      let r = "";
      if (this.ruleForm.community_id) {
        this.community_id.forEach(element => {
          if (element.id == this.ruleForm.community_id) {
            r = element.communityname;
          }
        });
        return r;
      } else {
        return "";
      }
    },
    houseTitle: {
      set: function(value) {
        this.ruleForm.title = value; 
      },
      get: function() {
        if (!this.$route.query.id) {
          return (
            this.houseName +
            " " +
            this.ruleForm.bedroom +
            (this.ruleForm.bedroom ? "室" : "") +
            this.ruleForm.livingroon +
            (this.ruleForm.livingroon ? "厅" : "") +
            this.ruleForm.bathroom +
            (this.ruleForm.bathroom ? "卫" : "") +
            " " +
            this.ruleForm.area +
            (this.ruleForm.area ? "平米" : "") +
            " " +
            this.fixture +
            " " +
            this.ruleForm.price +
            (this.ruleForm.price ? "万" : "")
          );
        } else {
          return this.ruleForm.title;
        }
      }
    }
  },
  watch: {
    houseTitle (val) {
      this.ruleForm.title = val
    }
  },
  methods: {
    /* 搜索小区 */
    remoteMethod(query) {
      return new Promise((resolve, reject) => {
        this.temp = query;
        if (query !== "") {
          this.loading = true;
          getCommunity(query)
            .then(res => {
              this.community_id = res.data.data_like;
              if (this.$route.params.search) {
                this.ruleForm.community_id = this.community_id[0].id;
              }
              resolve(res);
            })
            .catch(error => {
              reject(error);
            });

          this.loading = false;
        } else {
          this.community_id = [];
          resolve();
        }
      });
    },
    blurMethod(e) {
      if (this.community_id.length == 0) {
        this.ruleForm.community_id = this.temp;
      }
    },

    /* 添加小区 */
    addCommunity() {
      /* 1 esf  2 rent 3 store */
      this.$router.push({
        name: "addcommunity",
        params: { type: 1, search: this.temp }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.title = this.houseTitle;
          salePublish("post", "", this.ruleForm).then(res => {
            if (res.data.code == 1) {
              if (this.$route.query.id) {
                this.$message.success("编辑成功");
              } else {
                this.$message.success("新增成功");
              }

              this.$router.push({ name: "secondhouselists" });
            }
          });
        } else {
          return false;
        }
      });
    },

    changeLogo(data, key) {
      this.ruleForm[key] = data;
    },
    changeImages(data) {
      this.ruleForm.images = data;
    },
    changeVideo(data) {
      this.ruleForm.video = data;
    },
    editors(content) {
      // console.log(content);
      this.ruleForm.description = content;
    }
  }
};
</script>

<style lang='scss' scoped>
</style>



